    '': {fmtFunc: printAttr()},
    '': {fmtFunc: printAttr()},
    '': {fmtFunc: printAttr({flag: true})},
    '': {pct:true, fmtFunc: printAttr({symbol: '*'})},
    '': {fmtFunc: printAttr()},
    '': {fmtFunc: printAttr()},
    // 
